/*-----------------------------------------------------------------------------------------------------
  [elements:page]
-------------------------------------------------------------------------------------------------------*/
html {
  scroll-behavior: smooth;
}

body {
  font-family: Arial, sans-serif;
  color: #000000;
  background: #F6F6F6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* heading */
h1, h2, h3, h4, h5, h6 {
  font-family: Verdana, Geneva, sans-serif;
  color: #000000;
}

/* link */
a {
  color: #000000;
}

a:hover {
  color: #000000;
  text-decoration: underline;
}

/* list */
ul,
ol {
  margin: 0;
}

ul li,
ol li {
  margin: 0;
}

/* img */
img {
  display: inline-block;
  max-width: 100%;
  font-style: italic;
}

/* blockquote */
blockquote {
  color: #000000;
  font-style: italic;
  font-family: Georgia, "Times New Roman", serif;
  font-size: 18px;
  line-height: 170%;
}

blockquote p:before {
  content: " “ ";
  color: #cccccc;
}

blockquote p:after {
  content: " ” ";
  color: #000000;
}

/* hr */
hr {
  background: #dddddd;
  border: none;
  height: 1px;
}

/* table */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

table th {
  border: 1px solid gray;
  padding: 0.2em;
  text-align: left;
}

table td {
  border: 1px solid gray;
  padding: 0.2em;
}

